.latest-news {
    background-color: #fff;
    margin: 5vh auto;
    h1 {
        font-weight: 400;
        text-align: center;
        font-size: 4.7rem;
        margin-bottom: 5rem;
    }
    .latest-news_wrapper {
        width: 85%;
        margin: 2.5vh auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 2.5rem;
        .news-article {
            display: flex;
            justify-content: flex-start;
            height: 570px;
            height: 60vh;
            align-items: center;
            flex-direction: column;
            .news-thumbnail {
                width: 100%;
                height: 330px;
                border-radius: 32px;
                cursor: pointer;
                background-size: cover;
            }
            .news-heading {
                font-weight: 400;
                font-size: 2.05rem;
                margin: 1.3rem 0;
                line-height: calc(2.05rem * 1.2);
                cursor: pointer;
                &:hover {
                    text-decoration: underline;
                }
            }
            .news-description {
                font-size: 1.075rem;
                text-align: left;
                line-height: calc(1.075rem * 1.45);
            }
        }
    }
    .news-button {
        background-color: #000;
        color: #fff;
        font-weight: 500;
        font-size: 0.9rem;
        border: none;
        outline: none;
        text-transform: uppercase;
        margin: 0 auto;
        display: flex;
        align-items: center;
        cursor: pointer;
        padding: 1.15rem 2.25rem;
        transition: .15s ease-in-out;
        margin-bottom: 0.75rem;
        letter-spacing: 0.25px;
        word-spacing: 2px;
        .arrow-icon {
            color: rgb(195, 139, 33);
            font-size: 1.5rem;
            margin-left: 1rem;
        }
        &:hover {
            background-color: rgb(17, 17, 17);
        }
    }
}